<template>
  <section class="exchange-confirmation">
    <TextHeader class="exchange-confirmation__header">
      {{ t('exchangeConfirmation.title') }}
    </TextHeader>
    <main class="exchange-confirmation__body">
      <ExchangeCoinInfo
        class="exchange-confirmation__body__info-item"
        :coin="sendCoin"
        :label="t('exchangeConfirmation.sendTitle')"
        :rate="sendCoin?.rate"
        :sum="4.123"
      />
      <ExchangeCoinInfo
        class="exchange-confirmation__body__info-item"
        :coin="receiveCoin"
        :label="t('exchangeConfirmation.receiveTitle')"
        :rate="receiveCoin?.rate"
        :sum="0.0929345"
      />

      <div class="exchange-confirmation__body-estimate">
        <component
          :is="isMobile ? 'div' : 'Tooltip'"
          :text="t('exchangeConfirmation.tooltip')"
          :show-arrow="false"
        >
          <div class="exchange-confirmation__body-estimate__content">
            <Icon name="clock"/>
            <Text size="14px" weight="16px" ml="9px" mt="3px">
              {{ t('exchangeConfirmation.estimatedArrival') }}
              <Text weight="bold">≈ 2 - 40 {{ t('common.minuteText', 10) }}</Text>
              <Text v-if="isMobile" variant="p" size="12px" type="muted" mt="12px">{{ t('exchangeConfirmation.tooltip') }}</Text>
            </Text>
          </div>
        </component>
      </div>
      <Input
        :label="t('exchangeConfirmation.payoutAddress', { coin: receiveCoin?.title || '' })"
        v-model="address"
        :error="error"
        error-text="Incorrect address format"
      />
    </main>
    <div class="exchange-confirmation__actions">
      <Checkbox
        v-model="termsPolicy"
        class="exchange-confirmation__actions-checkbox"
        :label="t('exchangeConfirmation.termsPolicy')"
      />
      <div class="exchange-confirmation__actions__confirmation">
        <Button
          class="exchange-confirmation__actions__confirmation-btn"
          type="primary"
          @click="makeDeposit"
        >{{ t('common.confirm') }}</Button>
        <Button
          class="exchange-confirmation__actions__confirmation-btn"
          @click="goBack"
        >{{ t('common.back') }}</Button>
      </div>
    </div>
  </section>
</template>

<script>
import { MODULES_NAMES } from '@/shared/common/modules'
import { MAIN_ROUTES_NAMES } from '@/shared/common/routes'
import { useI18n } from '@/shared/plugins/i18n'
import { useGetters } from '@/shared/plugins/vuex'
import { ref, watch } from 'vue'
import { useBreakpointsMedia } from '@/shared/plugins/breakpoints'
import { useGoBack, useRouter } from '@/shared/plugins/vue-router'
import Text from '@/shared/components/UIKit/Text/Text'
import Icon from '@/shared/components/Icon/Icon'
import Input from '@/shared/components/UIKit/Input'
import Button from '@/shared/components/UIKit/Button'
import Tooltip from '@/shared/components/UIKit/Tooltip'
import Checkbox from '@/shared/components/UIKit/Checkbox'
import TextHeader from '@/client/components/TextHeader'
import ExchangeCoinInfo from '@/client/views/ExchangeConfirmation/ExchangeCoinInfo'

export default {
  name: 'ExchangeConfirmation',
  components: {
    Icon,
    Text,
    Input,
    Button,
    Tooltip,
    Checkbox,
    TextHeader,
    ExchangeCoinInfo
  },
  setup () {
    const router = useRouter()
    const goBack = useGoBack({
      name: MAIN_ROUTES_NAMES.HOME
    })
    const { t } = useI18n()
    const { isMobile } = useBreakpointsMedia()
    const {
      getReceiveCoin: receiveCoin,
      getSendCoin: sendCoin
    } = useGetters(MODULES_NAMES.EXCHANGE)

    const address = ref('3P3QsMVK89JBNqZQv5zMAKG8FK3kJM4rjt')
    const termsPolicy = ref(false)
    const error = ref(false)

    watch(address, (nv) => {
      error.value = nv.length !== 8
    })

    const makeDeposit = () => {
      router.push({ name: MAIN_ROUTES_NAMES.DEPOSITS })
    }
    return {
      t,
      receiveCoin,
      termsPolicy,
      isMobile,
      sendCoin,
      address,
      error,
      goBack,
      makeDeposit
    }
  }
}
</script>

